import axios from "@/plugins/axios";

// 新增分组
export const addGroup = (data) => {
  return axios({
    url: `/group/add`,
    method: "post",
    params: data,
  });
};

// 删除分组
export const deleteGroup = (data) => {
  return axios({
    url: `/group/delete`,
    method: "post",
    params: data,
  });
};

// 修改分组
export const editGroup = (data) => {
  return axios({
    url: `/group/edit`,
    method: "post",
    params: data,
  });
};

// 查询分组
export const queryGroup = (data) => {
  return axios({
    url: "/group/all",
    method: "get",
    data,
  });
};

