<template>
  <el-container>
    <el-aside width="250px">
      <div style="margin-bottom: 10px">
        <el-button
          type="success"
          size="mini"
          icon="el-icon-plus"
          @click="openDialog(1)"
          >新增</el-button
        >
        <el-button
          type="primary"
          size="mini"
          icon="el-icon-edit"
          @click="openDialog(2)"
          >修改</el-button
        ><el-button
          type="danger"
          size="mini"
          icon="el-icon-delete"
          @click="deleteNode"
          >删除</el-button
        >
      </div>
      <el-tree
        ref="treeRef"
        :data="treeData"
        v-loading="treeDataLoading"
        element-loading-text="数据加载中"
        element-loading-spinner="el-icon-loading"
        node-key="id"
        default-expand-all
        highlight-current
        :expand-on-click-node="false"
        :props="defaultProps"
        @current-change="currentChange"
      >
      </el-tree>
    </el-aside>

    <!-- 内容主体 -->
    <el-main>
      <!-- 头部过滤 新增接口区域 -->
      <el-row>
        <el-form
          size="mini"
          :inline="true"
          :model="formInline"
          class="demo-form-inline"
        >
          <!-- <el-form-item label="路径">
            <el-input
              v-model.trim="formInline.path"
              placeholder="api路径"
            ></el-input>
          </el-form-item>
          <el-form-item label="接口状态">
            <el-select v-model="formInline.status" placeholder="接口状态">
              <el-option label="开发中" value="1"></el-option>
              <el-option label="已完成" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary">查询</el-button>
          </el-form-item> -->
          <el-form-item>
            <el-button type="primary" @click="(mode = 'add'), resetForm()"
              >新增接口</el-button
            >
          </el-form-item>
        </el-form>
      </el-row>

      <!-- 底部接口列表 / 接口详情区域 -->
      <el-row>
        <!-- 接口列表 -->
        <el-col :span="12">
          <el-table
            ref="tableRef"
            v-loading="interfaceListLoading"
            :data="interfaceList"
            highlight-current-row
            size="small"
            @row-click="rowClick"
          >
            <el-table-column prop="name" label="接口名称"> </el-table-column>
            <el-table-column prop="httpRequest.path" label="接口Path">
            </el-table-column>
            <el-table-column
              prop="httpRequest.method"
              label="请求方法"
              width="80px"
            >
            </el-table-column>
            <!-- <el-table-column prop="priority" label="优先级"> </el-table-column> -->
            <el-table-column prop="created" label="创建时间">
              <template #default="scope">
                {{ dateFormat("YYYY-mm-dd hh:MM", scope.row.created) }}
              </template>
            </el-table-column>
            <el-table-column prop="apiStatus" label="状态">
              <template #default="scope">
                {{
                  scope.row.apiStatus === "dev"
                    ? "开发中"
                    : scope.row.apiStatus === "done"
                    ? "已完成"
                    : "--"
                }}
              </template>
            </el-table-column>
          </el-table>
        </el-col>

        <!-- 查看 / 新增 / 预览 -->
        <el-col :span="11" :offset="1">
          <!-- 接口信息 -->
          <div class="interface-preview" v-if="mode === 'preview'">
            <!-- 有数据 -->
            <template v-if="currentInterfaceInfo.name">
              <el-row>
                <el-col>
                  <el-descriptions
                    border
                    :column="1"
                    size="mini"
                    label-width="100px"
                    :title="`接口名称: ${currentInterfaceInfo.name}`"
                  >
                    <template #extra>
                      <el-button
                        size="mini"
                        type="warning"
                        icon="el-icon-edit"
                        @click="editInterface"
                        >编辑</el-button
                      >
                      <el-button
                        size="mini"
                        type="danger"
                        icon="el-icon-delete"
                        @click="deleteInterface"
                        >删除</el-button
                      >
                    </template>
                    <!-- <el-descriptions-item label="接口名称">{{
                  currentInterfaceInfo.name
                }}</el-descriptions-item> -->
                    <el-descriptions-item label="接口路径">{{
                      currentInterfaceInfo.httpRequest.path
                    }}</el-descriptions-item>
                    <el-descriptions-item label="请求方法">{{
                      currentInterfaceInfo.httpRequest.method
                    }}</el-descriptions-item>
                    <el-descriptions-item label="接口状态">
                      {{
                        currentInterfaceInfo.apiStatus === "dev"
                          ? "开发中"
                          : "已完成"
                      }}
                    </el-descriptions-item>
                    <el-descriptions-item label="接口优先级">
                      {{ currentInterfaceInfo.priority }}
                    </el-descriptions-item>
                    <el-descriptions-item label="请求示例">
                      <!-- Header -->
                      <el-descriptions
                        v-if="currentInterfaceInfo.httpRequest.headers"
                        border
                        :column="2"
                        size="mini"
                        label-width="100px"
                        title="Header"
                      >
                        <template
                          v-for="(value, key) in currentInterfaceInfo
                            .httpRequest.headers"
                          :key="key"
                        >
                          <el-descriptions-item label="参数名">
                            {{ key }}
                          </el-descriptions-item>
                          <el-descriptions-item label="参数值">
                            {{ value }}
                          </el-descriptions-item>
                        </template>
                      </el-descriptions>

                      <!-- Query -->
                      <el-descriptions
                        v-if="
                          currentInterfaceInfo.httpRequest.queryStringParameters
                        "
                        border
                        :column="2"
                        size="mini"
                        label-width="100px"
                        title="Query"
                      >
                        <template
                          v-for="(value, key) in currentInterfaceInfo
                            .httpRequest.queryStringParameters"
                          :key="key"
                        >
                          <el-descriptions-item label="参数名">
                            {{ key }}
                          </el-descriptions-item>
                          <el-descriptions-item label="参数值">
                            {{ value }}
                          </el-descriptions-item>
                        </template>
                      </el-descriptions>

                      <!-- Body -->
                      <el-descriptions
                        v-if="
                          currentInterfaceInfo.httpRequest.bodyType ===
                          'application/x-www-form-urlencoded; charset=utf-8'
                        "
                        border
                        :column="2"
                        size="mini"
                        label-width="100px"
                        title="Body"
                      >
                        <template #extra>{{
                          currentInterfaceInfo.httpRequest.bodyType
                        }}</template>
                        <template
                          v-for="value in currentInterfaceInfo.httpRequest.body"
                          :key="value.key"
                        >
                          <el-descriptions-item label="参数名">
                            {{ value.key }}
                          </el-descriptions-item>
                          <el-descriptions-item label="参数值">
                            {{ value.value }}
                          </el-descriptions-item>
                        </template>
                      </el-descriptions>
                      <template v-else>
                        <el-row align="center">
                          <el-col class="preview-body-title" :span="2">
                            <span class="body-style">body</span>
                          </el-col>
                          <el-col :span="20" :offset="2">
                            <div class="body-type-style">
                              {{ currentInterfaceInfo.httpRequest.bodyType }}
                            </div>
                          </el-col>
                        </el-row>
                        <Editor
                          v-model="currentInterfaceInfo.httpRequest.body"
                          lang="json"
                          width="100%"
                          height="200"
                          readonly
                        ></Editor>
                      </template>
                    </el-descriptions-item>
                    <el-descriptions-item label="响应示例">
                      <json-viewer
                        :value="currentInterfaceInfo.httpResponse.body"
                        copyable
                        boxed
                        sort
                      />
                    </el-descriptions-item>
                  </el-descriptions>
                </el-col>
              </el-row>
            </template>

            <!-- 无数据 -->
            <template v-else>
              <el-empty description="暂无数据"></el-empty>
            </template>
          </div>

          <!-- 新建 / 编辑接口 -->
          <el-form
            v-else
            label-position="right"
            label-width="100px"
            :model="interfaceForm"
            size="mini"
          >
            <el-form-item v-if="mode === 'edit'" label="接口ID" required>
              {{ interfaceForm.id }}
            </el-form-item>
            <el-form-item label="接口名称" required>
              <el-input
                v-model.trim="interfaceForm.name"
                placeholder="请输入接口名称"
              ></el-input>
            </el-form-item>
            <el-form-item label="接口路径" required>
              <el-input
                v-model.trim="interfaceForm.httpRequest.path"
                placeholder="请输入接口路径"
                class="input-with-select"
              >
                <template #prepend>
                  <el-select
                    style="width: 110px"
                    placeholder="请选择"
                    v-model="interfaceForm.httpRequest.method"
                  >
                    <el-option
                      v-for="item in requestMethods"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </template>
              </el-input>
            </el-form-item>
            <el-form-item label="接口状态" required>
              <!-- <el-row> -->
              <el-col :span="12">
                <el-select
                  v-model="interfaceForm.apiStatus"
                  placeholder="接口状态"
                >
                  <el-option label="开发中" value="dev"></el-option>
                  <el-option label="已完成" value="done"></el-option>
                </el-select>
              </el-col>
              <el-col :span="12">
                <el-form-item label="接口优先级">
                  <el-input
                    placeholder="请输入接口优先级"
                    type="number"
                    v-model.trim="interfaceForm.priority"
                  ></el-input>
                </el-form-item>
              </el-col>
              <!-- </el-row> -->
            </el-form-item>
            <el-form-item label="Request">
              <el-tabs :activeName="avtiveTab">
                <el-tab-pane
                  v-for="item in tabList"
                  :key="item.label"
                  :label="item.label"
                  :name="item.label"
                >
                  <el-row>
                    <template v-if="item.label === 'Body'">
                      <el-dropdown
                        @command="handleCommand"
                        style="margin: 0 0 10px 0"
                        trigger="click"
                      >
                        <span class="el-dropdown-link">
                          {{ interfaceForm.httpRequest.bodyType
                          }}<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <template #dropdown>
                          <el-dropdown-menu>
                            <el-dropdown-item
                              command="application/json; charset=utf-8"
                              >application/json</el-dropdown-item
                            >
                            <el-dropdown-item
                              command="application/x-www-form-urlencoded; charset=utf-8"
                              >application/x-www-form-urlencoded</el-dropdown-item
                            >
                          </el-dropdown-menu>
                        </template>
                      </el-dropdown>
                    </template>
                    <template
                      v-if="
                        item.label === 'Body' &&
                        interfaceForm.httpRequest.bodyType ===
                          'application/json; charset=utf-8'
                      "
                    >
                      <el-col :span="24">
                        <Editor
                          v-model="interfaceForm.httpRequest.body"
                          lang="json"
                          width="100%"
                          height="300"
                        ></Editor>
                      </el-col>
                    </template>

                    <template v-else>
                      <template
                        v-for="i in item.label === 'Header'
                          ? reqHeader
                          : item.label === 'Query'
                          ? reqQuery
                          : item.label === 'Body'
                          ? reqBody
                          : []"
                        :key="i"
                      >
                        <el-col :span="11" style="margin: 0 0 10px 0">
                          <el-input
                            v-model.trim="i.key"
                            placeholder="参数名"
                          ></el-input>
                        </el-col>
                        <el-col :span="11" :push="2">
                          <el-input
                            v-model.trim="i.value"
                            placeholder="参数值"
                          ></el-input>
                        </el-col>
                      </template>
                    </template>
                  </el-row>
                  <el-row style="margin-top: 10px">
                    <el-button
                      v-if="
                        item.label === 'Header' ||
                        item.label === 'Query' ||
                        (item.label === 'Body' &&
                          interfaceForm.httpRequest.bodyType !==
                            'application/json; charset=utf-8')
                      "
                      type="primary"
                      @click="addParams(item.label)"
                      >添加 {{ item.label }} 参数</el-button
                    >
                  </el-row>
                </el-tab-pane>
              </el-tabs>
            </el-form-item>
            <el-form-item label="Response" required>
              <el-row>
                <el-col>
                  <el-button type="text" size="mini" @click="handleZip"
                    >压缩</el-button
                  >
                  <el-button type="text" size="mini" @click="handleFormat"
                    >格式化</el-button
                  >
                </el-col>
                <Editor
                  v-model="interfaceForm.httpResponse.body"
                  lang="json"
                  width="100%"
                  height="300"
                ></Editor>
              </el-row>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="subimtForm">提交</el-button>
              <!-- <el-button>重置</el-button> -->
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </el-main>

    <!-- 新增/修改弹框 -->
    <el-dialog
      :title="dialogType === 1 ? '新增节点' : '修改节点'"
      v-model="dialogVisible"
      width="25%"
      @closed="label = ''"
    >
      <el-form size="mini">
        <el-form-item label="父级分组" v-if="parentGroupId > 0">
          <div style="text-align: left; padding-left: 32px">
            {{ parentInfo.label }}
          </div>
        </el-form-item>
        <el-form-item label="节点名称" label-width="100px">
          <el-input v-model.trim="label" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button size="mini" @click="dialogVisible = false"
            >取 消</el-button
          >
          <el-button size="mini" type="primary" @click="submitResult(true)">{{
            dialogType === 1 ? "新 增" : "修 改"
          }}</el-button>
          <el-button
            v-if="dialogType === 1"
            size="mini"
            type="primary"
            @click="submitResult(false)"
            >新增至根目录</el-button
          >
        </span>
      </template>
    </el-dialog>
  </el-container>
</template>

<script>
import { Editor } from "bin-ace-editor";
import "brace/ext/emmet";
import "brace/ext/language_tools";
import "brace/mode/json";
import "brace/snippets/json";
import "brace/theme/chrome";
import "brace/theme/xcode";
import {
  queryGroup,
  addGroup,
  editGroup,
  deleteGroup,
  addInterface,
  getInterface,
  deleteInterface,
  editInterface,
} from "@/api/group";
import { requestMethods } from "@/common/constant";

export default {
  name: "Home",
  setup() {
    return {
      formInline: {
        path: "",
        status: "1",
      },
      defaultProps: {
        children: "children",
        label: "label",
      },
      // treeData: arr,
    };
  },

  components: { Editor },
  data() {
    return {
      // 当前模式  preview--> 预览   add --> 新增  edit --> 编辑
      mode: "preview",
      // 分组树状数据
      treeData: [],
      // 当前选中的分组id
      currentGroupId: null,
      // 父级id
      parentGroupId: -1,
      // 新增修改分组弹框
      dialogVisible: false,

      label: "",
      // 1 --> 新增分组  2 --> 修改分组
      dialogType: 1,
      // 分组信息加载loading
      treeDataLoading: false,
      // 父级分组信息
      parentInfo: {},
      // 接口表单数据
      interfaceForm: {
        name: "",
        groupId: null,
        priority: 0,
        apiStatus: "dev",
        httpRequest: {
          method: "GET",
          path: "",
          headers: {},
          queryStringParameters: {},
          body: "",
          bodyType: "application/json; charset=utf-8",
        },
        httpResponse: {
          statusCode: 200,
          body: "",
        },
      },
      // 请求方法列表
      requestMethods: [],
      // 默认激活的 tab 页
      avtiveTab: "Header",
      tabList: [{ label: "Header" }, { label: "Query" }, { label: "Body" }],
      // 请求 header 参数
      reqHeader: [{ key: "", value: "" }],
      // 请求 query 参数
      reqQuery: [{ key: "", value: "" }],
      // 请求 body 参数
      reqBody: [{ key: "", value: "" }],
      // 接口列表
      interfaceList: [],
      // 接口列表数据加载laoding
      interfaceListLoading: false,
      // 当前选中的接口信息
      currentInterfaceInfo: {},
    };
  },

  created() {
    this.queryGroup();
    this.requestMethods = requestMethods;
  },

  methods: {
    // 点击节点设置当前选中的分组id一级父级id
    currentChange(data, node) {
      const parentId = { ...node.parent.data }.id;

      if (parentId) {
        this.parentGroupId = parentId;
        this.parentInfo = { ...node.parent.data };
      } else {
        this.parentGroupId = -1;
      }
      if (this.currentGroupId !== data.id) {
        this.currentGroupId = data.id;
        this.queryInterfaceList();
        this.mode = "preview";
      }
    },

    // 新增分组
    addGroup(flag) {
      const params = {
        parentId: flag ? this.currentGroupId : -1,
        label: this.label,
      };
      addGroup(params).then(() => {
        this.queryGroup();
      });
    },

    // 删除分组
    deleteGroup() {
      const params = {
        id: this.currentGroupId,
      };
      deleteGroup(params).then(() => {
        this.$message.success("删除成功");
        this.queryGroup();
      });
    },

    // 修改分组
    editGroup() {
      const params = {
        id: this.currentGroupId,
        label: this.label,
      };
      editGroup(params).then(() => {
        this.queryGroup();
      });
    },

    // 查询分组
    queryGroup() {
      this.treeDataLoading = true;
      queryGroup()
        .then((res) => {
          this.treeData = res;
          if (res.length > 0) {
            // 获取第一个分组下的接口列表
            if (!this.currentGroupId) {
              this.currentGroupId = res[0].id;
            }
            this.$nextTick(() => {
              this.$refs.treeRef.setCurrentKey(this.currentGroupId);
            });
            this.queryInterfaceList();
          }
        })
        .finally(() => (this.treeDataLoading = false));
    },

    // 删除分组二次确认弹框
    deleteNode() {
      if (!this.currentGroupId) {
        this.$message.error("请先选择需要删除的分组");
        return;
      }
      this.$confirm("此操作将删除该分组及其下所有接口定义, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.deleteGroup();
        })
        .catch(() => {
          this.$message.info("已取消删除");
        });
    },

    // 打开修改新增弹框
    openDialog(type) {
      this.dialogType = type;
      if (type === 2 && !this.currentGroupId) {
        this.$message.error("请先选择需要修改的分组");
        return;
      }
      this.dialogVisible = true;
    },

    // 提交修改或新增
    submitResult(flag) {
      if (!this.label) return this.$message.error("请输入节点名称");
      if (this.dialogType === 1) {
        // 新增
        this.addGroup(flag);
      } else {
        // 修改
        this.editGroup();
      }
      this.dialogVisible = false;
    },

    // 查询分组列表
    queryInterfaceList() {
      this.interfaceListLoading = true;
      const params = {
        groupId: this.currentGroupId,
      };
      getInterface(params)
        .then((res) => {
          this.interfaceList = res;
          if (res.length > 0) {
            this.currentInterfaceInfo = JSON.parse(JSON.stringify(res[0]));
            this.$refs.tableRef.setCurrentRow(res[0]);
            if (
              this.currentInterfaceInfo.httpRequest.bodyType ===
                "application/x-www-form-urlencoded; charset=utf-8" &&
              this.currentInterfaceInfo.httpRequest.body.length > 0
            ) {
              const arr = this.currentInterfaceInfo.httpRequest.body
                .split("&")
                .map((item) => {
                  return item.split("=");
                });
              this.currentInterfaceInfo.httpRequest.body = [];
              arr.forEach((item) => {
                if (item.length === 2) {
                  this.currentInterfaceInfo.httpRequest.body.push({
                    key: item[0],
                    value: item[1],
                  });
                }
              });
            }
            this.currentInterfaceInfo.httpResponse.body = JSON.parse(
              JSON.parse(
                JSON.stringify(this.currentInterfaceInfo.httpResponse.body)
              )
            );
          } else {
            this.currentInterfaceInfo = {};
          }
        })
        .finally(() => (this.interfaceListLoading = false));
    },

    // 添加参数
    addParams(label) {
      if (label === "Header") {
        this.reqHeader.push({ key: "", value: "" });
      } else if (label === "Query") {
        this.reqQuery.push({ key: "", value: "" });
      } else if (label === "Body") {
        this.reqBody.push({ key: "", value: "" });
      }
    },

    // 切换请求body方式
    handleCommand(cmd) {
      this.interfaceForm.httpRequest.bodyType = cmd;
    },

    // 提交接口
    subimtForm() {
      // 获取提交参数
      const params = JSON.parse(JSON.stringify(this.interfaceForm));
      params.groupId = this.currentGroupId;
      if (!params.groupId) {
        this.$notify.error({
          message: "请选择接口分组",
          position: "top-left",
        });
        return;
      }
      if (!params.name) {
        this.$notify.error({
          message: "请输入接口名称",
          position: "top-right",
        });
        return;
      }

      if (!params.httpRequest.path) {
        this.$notify.error({
          message: "请输入接口路径",
          position: "top-right",
        });
        return;
      }
      // 组装 request 参数
      this.reqHeader.forEach((item) => {
        if (item.key && item.value) {
          params.httpRequest.headers[item.key] = item.value;
        }
      });
      this.reqQuery.forEach((item) => {
        if (item.key && item.value) {
          params.httpRequest.queryStringParameters[item.key] = item.value;
        }
      });
      if (
        params.httpRequest.bodyType ===
        "application/x-www-form-urlencoded; charset=utf-8"
      ) {
        let str = "";
        this.reqBody.forEach((item) => {
          if (item.key && item.value) {
            str += `${item.key}=${item.value}&`;
          }
        });
        params.httpRequest.body = str.substr(0, str.length - 1);
      }
      if (!params.httpResponse.body) {
        this.$notify.error({
          message: "请输入格式合法的 Response",
          position: "top-right",
        });
        return;
      }

      if (this.mode === "add") {
        addInterface(params)
          .then(() => {
            this.$message.success("新增接口成功");
          })
          .then(() => {
            this.queryInterfaceList();
            this.mode = "preview";
            this.resetForm();
          })
          .catch(() => {
            this.$message.error("添加失败, 请检查参数后重试");
          });
      } else if (this.mode === "edit") {
        editInterface(params)
          .then(() => {
            this.$message.success("修改接口成功");
          })
          .then(() => {
            this.queryInterfaceList();
            this.mode = "preview";
            this.resetForm();
          })
          .catch(() => {
            this.$message.error("修改失败, 请检查参数后重试");
          });
      }
    },

    // 点击行
    rowClick(row) {
      this.currentInterfaceInfo = JSON.parse(JSON.stringify(row));
      if (
        this.currentInterfaceInfo.httpRequest.bodyType ===
          "application/x-www-form-urlencoded; charset=utf-8" &&
        this.currentInterfaceInfo.httpRequest.body.length > 0
      ) {
        const arr = this.currentInterfaceInfo.httpRequest.body
          .split("&")
          .map((item) => {
            return item.split("=");
          });
        this.currentInterfaceInfo.httpRequest.body = [];
        arr.forEach((item) => {
          if (item.length === 2) {
            this.currentInterfaceInfo.httpRequest.body.push({
              key: item[0],
              value: item[1],
            });
          }
        });
      }
      this.currentInterfaceInfo.httpResponse.body = JSON.parse(
        JSON.parse(JSON.stringify(this.currentInterfaceInfo.httpResponse.body))
      );
      this.mode = "preview";
    },

    // 删除接口
    deleteInterface() {
      this.$confirm("是否删除该接口, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          const params = {
            id: this.currentInterfaceInfo.id,
          };
          deleteInterface(params).then(() => {
            this.$message.success("删除成功");
            this.queryInterfaceList();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },

    // 编辑接口
    editInterface() {
      this.interfaceForm.name = this.currentInterfaceInfo.name;
      this.interfaceForm.id = this.currentInterfaceInfo.id;
      this.interfaceForm.groupId = this.currentGroupId;
      this.interfaceForm.priority = this.currentInterfaceInfo.priority;
      this.interfaceForm.apiStatus = this.currentInterfaceInfo.apiStatus;
      this.interfaceForm.httpRequest.method =
        this.currentInterfaceInfo.httpRequest.method;
      this.interfaceForm.httpRequest.path =
        this.currentInterfaceInfo.httpRequest.path;
      this.interfaceForm.httpRequest.bodyType =
        this.currentInterfaceInfo.httpRequest.bodyType;
      this.interfaceForm.httpRequest.body =
        this.currentInterfaceInfo.httpRequest.body;

      this.reqHeader = [];
      this.reqQuery = [];
      this.reqBody = [];
      for (const key in this.currentInterfaceInfo.httpRequest.headers) {
        this.reqHeader.push({
          key,
          value: this.currentInterfaceInfo.httpRequest.headers[key],
        });
      }

      for (const key in this.currentInterfaceInfo.httpRequest
        .queryStringParameters) {
        this.reqQuery.push({
          key,
          value:
            this.currentInterfaceInfo.httpRequest.queryStringParameters[key],
        });
      }

      if (
        this.currentInterfaceInfo.httpRequest.bodyType ===
        "application/x-www-form-urlencoded; charset=utf-8"
      ) {
        const arr = this.currentInterfaceInfo.httpRequest.body
          .split("&")
          .map((item) => {
            return item.split("=");
          });
        this.currentInterfaceInfo.httpRequest.body = [];
        arr.forEach((item) => {
          if (item.length === 2) {
            this.reqBody.push({
              key: item[0],
              value: item[1],
            });
          }
        });
      }

      this.interfaceForm.httpResponse.body = JSON.stringify(
        this.currentInterfaceInfo.httpResponse.body
      );

      this.mode = "edit";
    },

    // 日期格式化
    dateFormat(fmt, d) {
      let ret;
      const date = new Date(d);
      const opt = {
        "Y+": date.getFullYear().toString(),
        "m+": (date.getMonth() + 1).toString(),
        "d+": date.getDate().toString(),
        "h+": date.getHours().toString(),
        "M+": date.getMinutes().toString(),
        "s+": date.getSeconds().toString(),
        // 有其他格式化字符需求可以继续添加，必须转化成字符串
      };
      for (const k in opt) {
        ret = new RegExp("(" + k + ")").exec(fmt);
        if (ret) {
          fmt = fmt.replace(
            ret[1],
            ret[1].length === 1 ? opt[k] : opt[k].padStart(ret[1].length, "0")
          );
        }
      }
      return fmt;
    },

    // 重置表单
    resetForm() {
      this.interfaceForm.name = "";
      this.interfaceForm.groupId = null;
      this.interfaceForm.priority = 0;
      this.interfaceForm.apiStatus = "dev";
      this.interfaceForm.httpRequest.method = "GET";
      this.interfaceForm.httpRequest.path = "";
      this.interfaceForm.httpRequest.headers = {};
      this.interfaceForm.httpRequest.queryStringParameters = {};
      this.interfaceForm.httpRequest.body = "";
      this.interfaceForm.httpRequest.bodyType =
        "application/json; charset=utf-8";
      this.interfaceForm.httpResponse.statusCode = 200;
      this.interfaceForm.httpResponse.body = "";
      this.reqHeader = [{ key: "", value: "" }];
      this.reqQuery = [{ key: "", value: "" }];
      this.reqBody = [{ key: "", value: "" }];
    },

    // 压缩
    handleZip() {
      this.interfaceForm.httpResponse.body = JSON.stringify(
        JSON.parse(this.interfaceForm.httpResponse.body),
        null,
        0
      );
    },

    // 格式化
    handleFormat() {
      this.interfaceForm.httpResponse.body = JSON.stringify(
        JSON.parse(this.interfaceForm.httpResponse.body),
        null,
        2
      );
    },
  },
};
</script>

